// action

// 
export function createAddchar (data) {
    return {type: 'addchar', data}
}
export function createImpchar (data) {
    return {type: 'impchar', data}
}
export function createChangechar (data) {
    return {type: 'changechar', data}
}
export function createDelchar (data) {
    return {type: 'delchar', data}
}

