import React, { useEffect, useState, Fragment } from 'react'
import { useParams } from 'react-router-dom'
import { connect } from 'react-redux'
// import SChoose from '../../components/SChoose'
import SDamage from '../../components/SDamage'
import SSuit from '../../components/SSuit'
import TopBar from '../../components/TopBar'
import { createChangechar } from '../../redux/addchar/action'
import './style.css'
import { getter, debounce } from '../../utils'
import MySelect from '../../components/MySelect'
import MyInput from '../../components/MyInput'
import Tips from '../../components/Tips'
// import second from ''

let toredux

function BDamage({ chars, changechar }) {

  let Params = useParams()
  let [char, setChar] = useState({})
  let [isShow, setIsShow] = useState(false)
  let [isJoker, setIsJoker] = useState(false)
  useEffect(() => {
    let index = -1
    if (Params.id !== undefined) {
      chars.forEach((e, i) => {
        if (e.id === Params.id * 1) index = i
      })
    }
    if (index !== -1) {
      setChar(chars[index])
      setIsShow(true)
      chars[index].skill.map((s) => {
        if (s.s_jokerLike !== 0) setIsJoker(true)
      })
    }
  }, [])
  function f(e) {
    e.target.select()
  }

  function fn(c) {
    changechar(c)
  }

  // react, , 
  useEffect(() => {
    toredux = debounce(fn, 1000)
  }, [])

  function input(type, e, i) {
    let val = e.target.value
    if (val * 1 === -9876543) val = e.target.getAttribute('data-val')
    if (val === '' && e.target.type !== 'text') val = 0
    let tmp = val * 1
    if (!Object.is(tmp, NaN)) val = Math.abs(val * 1)

    switch (type) {
      case 'extraAttr_atk':
        char.extraAttr.extraAttr_atk = val
        break;
      case 'extraAttr_def':
        char.extraAttr.extraAttr_def = val
        break;
      case 'extraAttr_hp':
        char.extraAttr.extraAttr_hp = val
        break;
      case 'extraAttr_speed':
        char.extraAttr.extraAttr_speed = val
        break;
      case 'extraAttr_critRate':
        char.extraAttr.extraAttr_critRate = val
        break;
      case 'extraAttr_critDamage':
        char.extraAttr.extraAttr_critDamage = val
        break;
      case 'extraAttr_effect':
        char.extraAttr.extraAttr_effect = val
        break;
      case 'extraAttr_resist':
        char.extraAttr.extraAttr_resist = val
        break;
      case 'buff_addDamage':
        char.buff.buff_addDamage[1] = val
        break;
      case 'buff_atk':
        char.buff.buff_atk[4] = val
        break;
      case 'buff_def':
        char.buff.buff_def[4] = val
        break;
      case 'buff_iscritDamageGrowth':
        char.buff.buff_iscritDamageGrowth[2] = val
        break;
      case 'buff_isAtkGrowth':
        char.buff.buff_isAtkGrowth[2] = val
        break;
      case 'buff_isDefGrowth':
        char.buff.buff_isDefGrowth[2] = val
        break;
      case 'buff_isSpeedGrowthFinal':
        char.buff.buff_isSpeedGrowthFinal[2] = val
        break;
      case 'buff_isSpeedGrowthBase':
        char.buff.buff_isSpeedGrowthBase[2] = val
        break;
      case 'enemyWait_def':
        char.enemyWait.enemyWait_def = val
        break;
      case 'enemyFinal_hp':
        char.enemyFinal.enemyFinal_hp = val
        break;
      case 'enemyBuff_def':
        char.enemyBuff.enemyBuff_def[4] = val
        break;
      case 'enemyFinal_reduce':
        char.enemyFinal.enemyFinal_reduce = val
        break;
      case 'enemyFinal_guard':
        char.enemyFinal.enemyFinal_guard = val
        break;
      case 'debuffChoose':
        char.debuff.choose = val
        if (val === 1) char.damage.debuffA = 150
        if (val === 2) char.damage.debuffA = 30
        if (val === 3) char.damage.debuffA = 60
        break;
      case 'jibaoChoose':
        char.debuff.Jchoose = val
        if (val === 1) char.damage.debuffJA = 150
        if (val === 2) char.damage.debuffJA = 30
        if (val === 3) char.damage.debuffJA = 60
        break;
      case 'debuffJNum':
        char.damage.debuffJNum = val
        break;
      case 'debuffJB':
        char.damage.debuffJB = val
        break;
      case 'add_drink':
        char.add.add_drink = val
        break;
      case 'fixed_loseMyHp':
        char.skill[i].fixed.fixed_loseMyHp[1] = val
        break;
      case 'chooseDamage_normal':
        char.skill[i].chooseDamage.chooseDamage_normal[2] = val
        break;
      case 'chooseDamage_per':
        char.skill[i].chooseDamage.chooseDamage_per[2] = val
        break;
      case 'chooseThroughout_normal':
        char.skill[i].chooseThroughout.chooseThroughout_normal[2] = val
        break;
      case 's_addDamageA':
        char.skill[i].s_addDamageA = val
        break;
      case 's_throughout':
        char.skill[i].s_throughout = val
        break;
      case 's_damage':
        char.skill[i].s_damage = val
        break;
      case 's_fix':
        char.skill[i].s_fix = val
        break;
      // case '':
      //   char.skill[i].
      //   break;
      // case '':
      //   char.skill[i].
      //   break;
      // case '':
      //   char.skill[i].
      //   break;
      // case '':
      //   char.skill[i].
      //   break;
      case '':

        break;
      case '':

        break;
      case '':

        break;
      case '':

        break;
      case '':

        break;
      case '':

        break;
      case '':

        break;
      case '':

        break;
      case '':

        break;

      default:
        break;
    }
    setChar({ ...char })
    toredux(char)
  }
  function buff(type, e, i) {
    let val = e.target.checked
    switch (type) {
      case 'atkbuff':
        char.buff.buff_atk[0] = val ? 50 : 0
        break;
      case 'batkbuff':
        char.buff.buff_atk[0] = val ? 75 : 0

        break;
      case 'courage':
        char.buff.buff_atk[1] = val ? 30 : 0
        char.buff.buff_def[1] = val ? 30 : 0
        break;
      case 'bold':
        char.buff.buff_atk[2] = val ? 10 : 0
        char.buff.buff_def[2] = val ? 10 : 0
        break;
      case 'ratkbuff':
        char.buff.buff_atk[3] = val ? -50 : 0
        break;
      case 'defbuff':
        char.buff.buff_def[0] = val ? 60 : 0
        break;
      case 'rdefbuff':
        char.buff.buff_def[3] = val ? -70 : 0
        break;
      case 'rdbuff':
        char.buff.buff_critDamage[0] = val ? 50 : 0
        break;
      case 'speedbuff':
        char.buff.buff_speed = val ? 30 : 0
        break;
      case 'tragetbuff':
        char.buff.buff_target = val ? 15 : 0
        break;
      case 'restrain':
        char.buff.buff_refrain = val ? 10 : 0
        break;
      case 'anger':
        char.buff.buff_addDamage[0] = val ? 30 : 0
        break;
      case 'edefbuff':
        char.enemyBuff.enemyBuff_def[0] = val ? 50 : 0
        break;
      case 'epoli':
        char.enemyBuff.enemyBuff_def[1] = val ? 30 : 0
        break;
      case 'ekuangqi':
        char.enemyBuff.enemyBuff_def[2] = val ? 10 : 0
        break;
      case 'epojia':
        char.enemyBuff.enemyBuff_def[3] = val ? -50 : 0
        break;
      case 'joker':
        char.add.add_joker = val ? 3 : 0
        break;
      case 'throughout':
        char.skill[i].chooseThroughout.chooseThroughout_suit = val ? 15 : 0
        break;
      case 'zdyzs':
        char.skill[i].chooseDamage.chooseDamage_is[2] = val ? 1 : 0
        break;
      case 'zdyct':
        char.skill[i].chooseThroughout.chooseThroughout_is[2] = val ? 1 : 0
        break;
      case 'zdybs':
        char.skill[i].s_critDamage[2] = val ? 1 : 0
        break;
      // case 'joker':
      //   char.skill[i].
      //   break;
      // case 'joker':
      //   char.skill[i].
      //   break;
      // case 'joker':
      //   char.skill[i].
      //   break;

      default:
        break;
    }
    setChar({ ...char })
    toredux(char)
  }
  // function click() {
  //   console.log(getter('usual', char));
  // }
  return (
    isShow ? <div className='bdamage wrapper'>
      <div className='left'>
        <TopBar></TopBar>
        <SSuit char={char} isdamage={true}></SSuit>
        <SDamage char={char}></SDamage>
      </div>
      <div className='main border'>
        <div>
          <h2></h2>
          {/* <button onClick={click}>+</button> */}
          <div className='need sborder row'>
            <h3 className='col-12'><Tips css='t-2'></Tips></h3>
            <div className='col-4'>%
              <input onChange={e => input('extraAttr_atk', e)} value={char.extraAttr.extraAttr_atk} className='inp-1' type="number" onFocus={f} />
              <input onChange={e => input('extraAttr_atk', e)} max={350} value={char.extraAttr.extraAttr_atk} className='inp-3' type="range" />
            </div>
            <div className='col-4'>%
              <input onChange={e => input('extraAttr_def', e)} value={char.extraAttr.extraAttr_def} className='inp-1' type="number" onFocus={f} />
              <input onChange={e => input('extraAttr_def', e)} max={350} value={char.extraAttr.extraAttr_def} className='inp-3' type="range" />
            </div>
            <div className='col-4'>%
              <input onChange={e => input('extraAttr_hp', e)} value={char.extraAttr.extraAttr_hp} className='inp-1' type="number" onFocus={f} />
              <input onChange={e => input('extraAttr_hp', e)} max={350} value={char.extraAttr.extraAttr_hp} className='inp-3' type="range" />
            </div>
            <div className='col-4'> &nbsp;
              <input onChange={e => input('extraAttr_speed', e)} value={char.extraAttr.extraAttr_speed} className='inp-1' type="number" onFocus={f} />
              <input onChange={e => input('extraAttr_speed', e)} max={200} value={char.extraAttr.extraAttr_speed} className='inp-3' type="range" />
            </div>
            <div className='col-4'>%
              <input onChange={e => input('extraAttr_critRate', e)} value={char.extraAttr.extraAttr_critRate} className='inp-1' type="number" onFocus={f} />
              <input onChange={e => input('extraAttr_critRate', e)} max={100} value={char.extraAttr.extraAttr_critRate} className='inp-3' type="range" />
            </div>
            <div className='col-4'> %
              <input onChange={e => input('extraAttr_critDamage', e)} value={char.extraAttr.extraAttr_critDamage} className='inp-1' type="number" onFocus={f} />
              <input onChange={e => input('extraAttr_critDamage', e)} max={200} value={char.extraAttr.extraAttr_critDamage} className='inp-3' type="range" />
            </div>
            <div className='col-4'> %
              <input onChange={e => input('extraAttr_effect', e)} value={char.extraAttr.extraAttr_effect} className='inp-1' type="number" onFocus={f} />
              <input onChange={e => input('extraAttr_effect', e)} max={250} value={char.extraAttr.extraAttr_effect} className='inp-3' type="range" />
            </div>
            <div className='col-4'> %
              <input onChange={e => input('extraAttr_resist', e)} value={char.extraAttr.extraAttr_resist} className='inp-1' type="number" onFocus={f} />
              <input onChange={e => input('extraAttr_resist', e)} max={250} value={char.extraAttr.extraAttr_resist} className='inp-3' type="range" />
            </div>
          </div>


          <div className='buff row sborder'>
            <h3 className='col-12'>buff</h3>
            <div className='col-2'>
              {/* inputlabel js */}
              <input id="atkbuff" type="checkbox" onChange={(e) => buff('atkbuff', e)} checked={char.buff.buff_atk[0] === 50} />
              <label htmlFor="atkbuff">buff</label>
            </div>
            <div className='col-2'>
              <input id="batkbuff" type="checkbox" onChange={(e) => buff('batkbuff', e)} checked={char.buff.buff_atk[0] === 75} />
              <label htmlFor="batkbuff">buff</label>
            </div>
            <div className='col-2'>
              <input id="courage" type="checkbox" onChange={(e) => buff('courage', e)} defaultChecked={char.buff.buff_atk[1] === 30} />
              <label htmlFor="courage"></label>
            </div>
            <div className='col-2'>
              <input id="bold" type="checkbox" onChange={(e) => buff('bold', e)} defaultChecked={char.buff.buff_atk[2] === 10} />
              <label htmlFor="bold"></label>
            </div>
            <div className='col-2'>
              <input id="ratkbuff" type="checkbox" onChange={(e) => buff('ratkbuff', e)} defaultChecked={char.buff.buff_atk[3] === -50} />
              <label htmlFor="ratkbuff">debuff</label>
            </div>
            {/*  */}
            <div className='col-2'>
              <input id="defbuff" type="checkbox" onChange={(e) => buff('defbuff', e)} defaultChecked={char.buff.buff_def[0] === 60} />
              <label htmlFor="defbuff">buff</label>
            </div>
            <div className='col-2'>
              <input id="rdefbuff" type="checkbox" onChange={(e) => buff('rdefbuff', e)} defaultChecked={char.buff.buff_def[3] === -70} />
              <label htmlFor="rdefbuff">debuff</label>
            </div>
            {/*  */}
            <div className='col-2'>
              <input id="rdbuff" type="checkbox" onChange={(e) => buff('rdbuff', e)} defaultChecked={char.buff.buff_critDamage[0] === 50} />
              <label htmlFor="rdbuff">buff</label>
            </div>
            <div className='col-2'>
              <input id="speedbuff" type="checkbox" onChange={(e) => buff('speedbuff', e)} defaultChecked={char.buff.buff_speed === 30} />
              <label htmlFor="speedbuff">buff</label>
            </div>

            <div className='col-2'>
              <input id="restrain" type="checkbox" onChange={(e) => buff('restrain', e)} defaultChecked={char.buff.buff_refrain === 10} />
              <label htmlFor="restrain"></label>
            </div>
            <div className='col-2'>
              <input id="anger" type="checkbox" onChange={(e) => buff('anger', e)} defaultChecked={char.buff.buff_addDamage[0] === 30} />
              <label htmlFor="anger"></label>
            </div>

          </div>


          <div className='generic row sborder'>
            <h3 className='col-12'><Tips css='t-2'><br /></Tips></h3>
            <div className='col-6'>%<Tips></Tips>
              <input onChange={e => input('buff_atk', e)} value={char.buff.buff_atk[4]} className='inp-1' type="number" onFocus={f} />
              <input onChange={e => input('buff_atk', e)} value={char.buff.buff_atk[4]} className='inp-3' type="range" />
            </div>
            <div className='col-6'><Tips></Tips>
              <input onChange={e => input('buff_addDamage', e)} value={char.buff.buff_addDamage[1]} className='inp-1' type="number" onFocus={f} />
              <input onChange={e => input('buff_addDamage', e)} value={char.buff.buff_addDamage[1]} className='inp-3' type="range" />
            </div>
            <div className='col-6'>%<Tips></Tips>
              <input onChange={e => input('buff_def', e)} value={char.buff.buff_def[4]} className='inp-1' type="number" onFocus={f} />
              <input onChange={e => input('buff_def', e)} value={char.buff.buff_def[4]} className='inp-3' type="range" />
            </div>



            {
              char.buff.buff_iscritDamageGrowth[1] ? <div className='col-12'><Tips po>{char.buff.buff_iscritDamageGrowth[0] ? char.buff.buff_iscritDamageGrowth[0] : 's2'}</Tips>
                <input onChange={e => input('buff_iscritDamageGrowth', e)} value={char.buff.buff_iscritDamageGrowth[2]} className='inp-1' type="number" onFocus={f} />
                <input onChange={e => input('buff_iscritDamageGrowth', e)} value={char.buff.buff_iscritDamageGrowth[2]} className='inp-3' type="range" />
              </div> : ''
            }

            {
              char.buff.buff_isAtkGrowth[1] ? <div className='col-12'>%<Tips po>{char.buff.buff_isAtkGrowth[0] ? char.buff.buff_isAtkGrowth[0] : 's2'}</Tips>
                <input onChange={e => input('buff_isAtkGrowth', e)} value={char.buff.buff_isAtkGrowth[2]} className='inp-1' type="number" onFocus={f} />
                <input onChange={e => input('buff_isAtkGrowth', e)} value={char.buff.buff_isAtkGrowth[2]} className='inp-3' type="range" />
              </div> : ''
            }
            {
              char.buff.buff_isDefGrowth[1] ? <div className='col-12'>%<Tips po>{char.buff.buff_isAtkGrowth[0] ? char.buff.buff_isAtkGrowth[0] : ''}</Tips>
                <input onChange={e => input('buff_isDefGrowth', e)} value={char.buff.buff_isDefGrowth[2]} className='inp-1' type="number" onFocus={f} />
                <input onChange={e => input('buff_isDefGrowth', e)} value={char.buff.buff_isDefGrowth[2]} className='inp-3' type="range" />
              </div> : ''
            }
            {
              char.buff.buff_isSpeedGrowthFinal[1] ? <div className='col-12'><Tips po>{char.buff.buff_isSpeedGrowthFinal[0] ? char.buff.buff_isSpeedGrowthFinal[0] : ''}</Tips>
                <input onChange={e => input('buff_isSpeedGrowthFinal', e)} value={char.buff.buff_isSpeedGrowthFinal[2]} className='inp-1' type="number" onFocus={f} />
                <input onChange={e => input('buff_isSpeedGrowthFinal', e)} value={char.buff.buff_isSpeedGrowthFinal[2]} className='inp-3' type="range" />
              </div> : ''
            }
            {
              char.outfit.outfit_suit[0] === 4 ? <div className='col-12'><Tips></Tips>
                <input onChange={e => input('buff_isSpeedGrowthBase', e)} value={char.buff.buff_isSpeedGrowthBase[2]} className='inp-1' type="number" onFocus={f} />
                <input onChange={e => input('buff_isSpeedGrowthBase', e)} value={char.buff.buff_isSpeedGrowthBase[2]} className='inp-3' type="range" />
              </div> : ''
            }

          </div>


          <div className='bombdamage row sborder'>
            <h3 className='col-12'></h3>
            <div className='col-12 '>
              {/* <select onChange={e => input('debuffChoose', e)} value={char.debuff.choose} className='select-2'>
                <option value={0}>debuff</option>
                <option value={1}></option>
                <option value={2}></option>
                <option value={3}></option>
              </select> */}
              <MySelect val={char.debuff.choose} fun={input} style='select-2' shallow type={'debuffChoose'}>
                <li value={0}>debuff</li>
                <li value={1}></li>
                <li value={2}></li>
                <li value={3}></li>
              </MySelect>
              {char.debuff.choose ? <span> &nbsp;{getter('debuffDamage', char)}</span> : ''}
            </div>


            <div className='col-3'>
              {/* <select onChange={e => input('jibaoChoose', e)} value={char.debuff.Jchoose} className='select-2' name="" id="">
                <option value={0}></option>
                <option value={1}></option>
                <option value={2}></option>
                <option value={3}></option>
              </select> */}
              <MySelect val={char.debuff.Jchoose} fun={input} style='select-2' shallow type={'jibaoChoose'}>
                <li value={0}></li>
                <li value={1}></li>
                <li value={2}></li>
                <li value={3}></li>
              </MySelect>
            </div>
            <div className='col-3'>
              {char.debuff.Jchoose === 1 ? '' : 'debuff'}
              <input onChange={e => input('debuffJNum', e)} value={char.damage.debuffJNum} className='inp-2' type="number" onFocus={f} />
            </div>
            <div className='col-4'>
              <span></span>

              <MyInput val={char.damage.debuffJB} shallow fun={input} type={'debuffJB'} style={'i4'}>
                <li data-val={1.3} value={-9876543}>-</li>
                <li data-val={1.05} value={-9876543}>-</li>
                <li data-val={1.1} value={-9876543}>-</li>
                <li data-val={1.2} value={-9876543}>-</li>
                <li value={1}>/-</li>
                <li data-val={1.1} value={-9876543}>-</li>
              </MyInput>
              {/* <input onChange={e => input('debuffJB', e)} value={char.damage.debuffJB} className='inp-2' type="number" onFocus={f} /> */}
            </div>
            <div className='col-2'>
              {char.debuff.Jchoose ? <span>{getter('boomDamage', char)}</span> : ''}
            </div>


            <div className='col-12'><span></span>
              {/* <input onChange={e => input('add_drink', e)} value={char.add.add_drink} className='inp-1' type="number" onFocus={f} /> */}
              <MyInput val={char.add.add_drink} shallow fun={input} type={'add_drink'} style={'inp-4'}>
                <li value={30}></li>
                <li value={45}></li>
                <li value={40}></li>
              </MyInput>
              {char.add.add_drink ? <span>{parseInt(getter('final_atk', char) * char.add.add_drink / 100 * 1.871
                / (getter('enemyFinal_def', char) * 0.3 / 300 + 1))}</span> : ''}
            </div>
          </div>


          <div className='enemy row sborder'>
            <h3 className='col-12'></h3>
            <div className='col-6'>
              <input onChange={e => input('enemyWait_def', e)} value={char.enemyWait.enemyWait_def} className='inp-2' type="number" onFocus={f} />
              <input onChange={e => input('enemyWait_def', e)} max={4000} min={200} step={10} value={char.enemyWait.enemyWait_def} className='inp-3' type="range" />

            </div>

            <div className='col-6'>%
              <input onChange={e => input('enemyBuff_def', e)} value={char.enemyBuff.enemyBuff_def[4]} className='inp-1' type="number" onFocus={f} />
              <input onChange={e => input('enemyBuff_def', e)} value={char.enemyBuff.enemyBuff_def[4]} className='inp-3' type="range" />
            </div>

            <div className='col-6'>
              <input onChange={e => input('enemyFinal_reduce', e)} value={char.enemyFinal.enemyFinal_reduce} className='inp-1' type="number" onFocus={f} />
              <input onChange={e => input('enemyFinal_reduce', e)} value={char.enemyFinal.enemyFinal_reduce} className='inp-3' type="range" />
            </div>
            <div className='col-6'>
              <input onChange={e => input('enemyFinal_guard', e)} value={char.enemyFinal.enemyFinal_guard} className='inp-1' type="number" onFocus={f} />
              <input onChange={e => input('enemyFinal_guard', e)} value={char.enemyFinal.enemyFinal_guard} className='inp-3' type="range" />
            </div>

            <div className='col-2'>
              <input id="edefbuff" type="checkbox" onChange={(e) => buff('edefbuff', e)} defaultChecked={char.enemyBuff.enemyBuff_def[0] === 50} />
              <label htmlFor="edefbuff">buff</label>
            </div>
            <div className='col-2'>
              <input id="epoli" type="checkbox" onChange={(e) => buff('epoli', e)} defaultChecked={char.enemyBuff.enemyBuff_def[1] === 30} />
              <label htmlFor="epoli"></label>
            </div>
            <div className='col-2'>
              <input id="ekuangqi" type="checkbox" onChange={(e) => buff('ekuangqi', e)} defaultChecked={char.enemyBuff.enemyBuff_def[2] === 10} />
              <label htmlFor="ekuangqi"></label>
            </div>

            <div className='col-2'>
              <input id="epojia" type="checkbox" onChange={(e) => buff('epojia', e)} defaultChecked={char.enemyBuff.enemyBuff_def[3] === -50} />
              <label htmlFor="epojia">debuff</label>
            </div>
            <div className='col-2'>
              <input id="tragetbuff" type="checkbox" onChange={(e) => buff('tragetbuff', e)} defaultChecked={char.buff.buff_target === 15} />
              <label htmlFor="tragetbuff"></label>
            </div>
            <div className='col-2'>
              <input id="joker" type="checkbox" onChange={(e) => buff('joker', e)} defaultChecked={char.add.add_joker === 3} />
              <label htmlFor="joker"></label>
            </div>
            {
              isJoker || char.add.add_joker ? <div className='col-6'>
                <input onChange={e => input('enemyFinal_hp', e)} value={char.enemyFinal.enemyFinal_hp} className='inp-2' type="number" onFocus={f} />
                <input onChange={e => input('enemyFinal_hp', e)} max={50000} min={5000} step={100} value={char.enemyFinal.enemyFinal_hp} className='inp-3' type="range" />

              </div> : ''
            }

          </div>




          {
            char.skill.map((s, i) => {
              // if(s.s_jokerLike) setIsJoker(1)
              return (
                <div className='skill sborder row' key={i}>
                  <h3 className='col-12'>{s.sName}</h3>
                  <div className='col-4 guanchuan'>
                    <input id={'throughout' + i} type="checkbox" onChange={(e) => buff('throughout', e, i)} defaultChecked={s.chooseThroughout.chooseThroughout_suit === 15} />
                    <label htmlFor={'throughout' + i}></label>
                  </div>
                  {
                    s.chooseDamage.chooseDamage_is[1] ? <div className='col-4 guanchuan'>
                      <input id={"zdyzs" + i} type="checkbox" onChange={(e) => buff('zdyzs', e, i)} defaultChecked={s.chooseDamage.chooseDamage_is[2] === 1} />
                      <label htmlFor={"zdyzs" + i}><Tips>{s.chooseDamage.chooseDamage_is[0] ? s.chooseDamage.chooseDamage_is[0] : ''}</Tips></label>
                    </div> : ''
                  }
                  {
                    s.chooseThroughout.chooseThroughout_is[1] ? <div className='col-4 guanchuan'>
                      <input id={"zdyct" + i} type="checkbox" onChange={(e) => buff('zdyct', e, i)} defaultChecked={s.chooseThroughout.chooseThroughout_is[2] === 1} />
                      <label htmlFor={"zdyct" + i}><Tips>{s.chooseThroughout.chooseThroughout_is[0] ? s.chooseThroughout.chooseThroughout_is[0] : ''}</Tips></label>
                    </div> : ''
                  }
                  {
                    s.s_critDamage[1] ? <div className='col-4 guanchuan'>
                      <input id={"zdybs" + i} type="checkbox" onChange={(e) => buff('zdybs', e, i)} defaultChecked={s.s_critDamage[2] === 1} />
                      <label htmlFor={"zdybs" + i}><Tips>{s.s_critDamage[0] ? s.s_critDamage[0] : ''}</Tips></label>
                    </div> : ''
                  }
                  {
                    s.fixed.fixed_loseMyHp[0] ? <div className='col-12 losehp'><span> <Tips>s3</Tips></span>
                      <MyInput val={s.fixed.fixed_loseMyHp[1]} fun={input} i={i} type={'fixed_loseMyHp'} style={'inp-2'}>
                        <li value={Math.floor(getter('final_hp', char) * 0.5)}>50%</li>
                        <li value={Math.floor(getter('final_hp', char) * 0.7)}>70%</li>
                        <li value={Math.floor(getter('final_hp', char) * 0.9)}>90%</li>
                      </MyInput>
                      <span className='rangint'>
                        <input onChange={e => input('fixed_loseMyHp', e, i)} max={Math.floor(getter('final_hp', char))} min={1000} step={100} value={s.fixed.fixed_loseMyHp[1]} className='inp-3' type="range" />
                      </span>
                      {/* <input onChange={e => input('fixed_loseMyHp', e, i)} value={s.fixed.fixed_loseMyHp[1]} className='inp-2' type="number" onFocus={f} /> */}
                    </div> : ''
                  }
                  {
                    s.chooseDamage.chooseDamage_normal[1] ? <div className='col-12'><Tips po>{s.chooseDamage.chooseDamage_normal[0] ? s.chooseDamage.chooseDamage_normal[0] : 's3'}</Tips>
                      <input onChange={e => input('chooseDamage_normal', e, i)} value={s.chooseDamage.chooseDamage_normal[2]} className='inp-2' type="number" onFocus={f} />
                    </div> : ''
                  }
                  {
                    s.chooseDamage.chooseDamage_per[1] ? <div className='col-12'><Tips po>{s.chooseDamage.chooseDamage_per[0] ? s.chooseDamage.chooseDamage_per[0] : 's1'}</Tips>
                      <input onChange={e => input('chooseDamage_per', e, i)} value={s.chooseDamage.chooseDamage_per[2]} className='inp-1' type="number" onFocus={f} />
                      <input onChange={e => input('chooseDamage_per', e, i)} value={s.chooseDamage.chooseDamage_per[2]} className='inp-3' type="range" />
                    </div> : ''
                  }
                  {
                    s.chooseThroughout.chooseThroughout_normal[1] ? <div className='col-12'><Tips po>{s.chooseThroughout.chooseThroughout_normal[0] ? s.chooseThroughout.chooseThroughout_normal[0] : 's3'}</Tips>
                      <input onChange={e => input('chooseThroughout_normal', e, i)} value={s.chooseThroughout.chooseThroughout_normal[2]} className='inp-1' type="number" onFocus={f} />
                      <input onChange={e => input('chooseThroughout_normal', e, i)} value={s.chooseThroughout.chooseThroughout_normal[2]} step={s.chooseThroughout.chooseThroughout_normal[1] < 0.1 ? s.chooseThroughout.chooseThroughout_normal[1] * 1000 : 1}
                        max={Math.floor(101 / s.chooseThroughout.chooseThroughout_normal[1])} className='inp-3' type="range" />
                    </div> : ''
                  }



                  <div className='col-12'><Tips></Tips>
                    <input onChange={e => input('s_addDamageA', e, i)} value={s.s_addDamageA} className='inp-1' type="number" onFocus={f} />
                    <input onChange={e => input('s_addDamageA', e, i)} max={50} value={s.s_addDamageA} className='inp-3' type="range" />
                  </div>

                  <div className='col-12'><Tips po>aoe</Tips>
                    <input onChange={e => input('s_throughout', e, i)} value={s.s_throughout} className='inp-1' type="number" onFocus={f} />
                    <input onChange={e => input('s_throughout', e, i)} value={s.s_throughout} className='inp-3' type="range" />
                  </div>

                  <div className='col-6'><Tips po></Tips>
                    <input onChange={e => input('s_damage', e, i)} value={s.s_damage} className='inp-1' type="number" onFocus={f} />
                    <input onChange={e => input('s_damage', e, i)} value={s.s_damage} className='inp-3' type="range" />
                  </div>

                  <div className='col-6'><Tips></Tips>
                    <input onChange={e => input('s_fix', e, i)} value={s.s_fix} className='inp-2' type="number" onFocus={f} />
                    {/* <input onChange={e => input('s_fix', e, i)} value={s.s_fix} max={10000} className='inp-3' type="range" /> */}
                  </div>

                  <div className='col-4 h'><Tips po top={i === char.skill.length - 1}></Tips>&nbsp;&nbsp;&nbsp;{parseInt(getter('totalChooseDamage', char)[i] * 100)}%</div>
                  <div className='col-4 h'><Tips top2={i === char.skill.length - 1}><br />s3</Tips>&nbsp;&nbsp;&nbsp;{parseInt(100 - getter('totalChooseThroughout', char)[i] * 100)}%</div>
                  <div className='col-4 h'>&nbsp;&nbsp;&nbsp;{parseInt(getter('totalCritDamage', char)[i] * 100)}%</div>

                  {/* <div className='col-4 findamage'>{getter('damage', char)[i]}</div>
                  <div className='col-4 findamage'>{getter('miss', char)[i]}</div>
                  <div className='col-4 findamage'>{getter('usual', char)[i]}</div>


                  <div className='col-4 findamage'>{getter('damageSoul', char)[i]}</div>
                  <div className='col-4 findamage'>{getter('missSoul', char)[i]}</div>
                  <div className='col-4 findamage'>{getter('usualSoul', char)[i]}</div> */}

                  {/* <div className='col-4 findamage'>{parseInt(getter('final_atk', char) * s.s_add * 1.871
                    / (getter('enemyFinal_def', char) * 0.3 / 300 + 1))}</div> */}

                </div>
              )
            })
          }

        </div>

        <div className='tablewrap'>
          <div>
            <table className="showdamage">
              <thead>
                <tr>
                  <th> </th>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                </tr>
              </thead>
            </table>
          </div>
          <div className='damagebody'>
            <table className="showdamage">
              <tbody>
                {
                  char.skill.map((s, i) => {
                    return (
                      s.mag.SoulMAG ?
                        <Fragment key={i}>
                          <tr>
                            <td>{s.sName}</td>
                            <td>{parseInt(getter('damage', char)[i])}</td>
                            <td>{parseInt(getter('miss', char)[i])}</td>
                            <td>{getter('usual', char)[i]}</td>
                            {s.s_add ? <td>{parseInt(getter('final_atk', char) * s.s_add / 100 * 1.871
                              / (getter('enemyFinal_def', char) * 0.3 / 300 + 1))}
                            </td> : <td></td>}
                          </tr>
                          <tr>
                            <td>{s.sName} </td>
                            <td>{parseInt(getter('damageSoul', char)[i])}</td>
                            <td>{parseInt(getter('missSoul', char)[i])}</td>
                            <td>{getter('usualSoul', char)[i]}</td>
                            {s.s_add ? <td>/</td> : <td></td>}
                          </tr>
                        </Fragment>
                        :
                        <tr key={i}>
                          <td>{s.sName}</td>
                          <td>{parseInt(getter('damage', char)[i])}</td>
                          <td>{parseInt(getter('miss', char)[i])}</td>
                          <td>{getter('usual', char)[i]}</td>
                          {s.s_add ? <td>{parseInt(getter('final_atk', char) * s.s_add / 100 * 1.871
                            / (getter('enemyFinal_def', char) * 0.3 / 300 + 1))}
                          </td> : <td></td>}
                        </tr>
                    )
                  })
                }
              </tbody>
            </table>
          </div>
        </div>


      </div>

    </div>

      : <div><br /><br /><h1></h1></div>
  )
}


// mapDispatchToProps , 
const mapDispatch = {
  changechar: createChangechar,
  // jiaAsync: createAsyncAdd
}

// connect, ui
// export default connect(mapStateToProps, mapDispatchToProps)(MyRedux)
export default connect(mapStateToProps, mapDispatch)(BDamage)

// propsui,  ===> <UI a={1} />
function mapStateToProps(state) {
  return { chars: state.addchar_reducer }
}